},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row justify-content-center align-items-center my-3 mb-5",attrs:{"data-aos":"zoom-in"}},[_c('div',{staticClass:"col-12 col-lg-10"},[_c('div',{staticClass:"cajon color-terciario p-4"},[_c('p',{staticClass:"mb-0"},[_vm._v("El análisis de estas interacciones no solo facilita la detección oportuna de posibles inconformidades, sino que también permite identificar oportunidades de mejora, ajustar estrategias comunicacionales y responder de forma inmediata ante eventuales situaciones que puedan afectar la reputación. Por ejemplo, si un cliente publica una queja sobre un producto defectuoso, la empresa puede intervenir rápidamente con una respuesta pública adecuada y una solución efectiva, demostrando compromiso con la calidad y la satisfacción del cliente. Esta retroalimentación espontánea y directa se convierte así en un insumo valioso para fortalecer la relación con los usuarios y consolidar una imagen positiva de la organización.")])])])])
},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row justify-content-center align-items-center mb-5",attrs:{"data-aos":"zoom-in"}},[_c('div',{staticClass:"col-12 col-lg-10"},[_c('div',{staticClass:"row justify-content-center align-items-center mb-2 ordenar_div",attrs:{"data-aos":"zoom-in"}},[_c('div',{staticClass:"col-lg-10"},[_c('p',[_vm._v("Un ejemplo de esta estrategia puede observarse en el caso de una marca de ropa que monitorea activamente sus menciones en redes sociales. Al detectar una publicación de un cliente insatisfecho que recibió una prenda con defecto, el equipo de atención al cliente responde de manera inmediata, ofreciendo una disculpa pública y gestionando el reemplazo del producto. Esta acción no solo soluciona el inconveniente puntual, sino que también envía un mensaje claro al resto de la comunidad digital sobre el compromiso de la empresa con la calidad y la atención al cliente. Este tipo de intervención refleja los principios del modelo SERVQUAL (Parasuraman, Zeithaml & Berry, 1988), en tanto reconoce que la percepción del servicio puede gestionarse de forma efectiva mediante respuestas ágiles y empáticas en los canales donde los consumidores se expresan con mayor frecuencia.")]),_c('div',{staticClass:"row align-items-center"},[_c('div',{staticClass:"col-2 d-none d-md-block"},[_c('figure',[_c('img',{staticClass:"mx-auto w-auto",attrs:{"src":require("@/assets/curso/temas/tema2/img_32.svg"),"alt":"Imagen decorativa"}})])]),_c('div',{staticClass:"col-10"},[_c('p',[_vm._v("Este tipo de práctica se ilustra claramente en el siguiente caso, que muestra cómo una empresa puede convertir una situación crítica en una oportunidad para fortalecer su reputación y fidelizar a sus clientes a través de la gestión estratégica de redes sociales.")])])])]),_c('div',{staticClass:"col-md-6 col-lg-2 col-8 mb-3 mb lg-0 arriba"},[_c('figure',[_c('img',{staticClass:"mx-auto w-auto",attrs:{"src":require("@/assets/curso/temas/tema2/img_18.svg"),"a